body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  display: -webkit-flex;
  display: flex;
  height: 100vh;
}
.outer-wrapper {
  margin: auto; }

.game-params {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }
  .game-params .group {
    display: -webkit-flex;
    display: flex; }
  .game-params label {
    width: 50%; }
  .game-params input {
    width: 50%;
    border-radius: 4px;
    border: 1px solid #ccc;
    padding: 5px; }

.tile-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }

.row {
  display: -webkit-flex;
  display: flex; }

.game-wrapper {
  display: inline-block;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: auto;
  margin: auto; }

.info-panel {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 15px 0; }
  .info-panel .new-game {
    cursor: pointer;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 8px; }
    .info-panel .new-game:hover {
      background: #ccc; }
  .info-panel .quantity {
    margin-left: auto; }

.result {
  font-size: 30px;
  color: #eee;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  width: 100%;
  background: rgba(0, 0, 0, 0.75);
  text-align: center;
  padding: 30px 0; }

.tile {
  border: 1px solid #999;
  background: #ccc;
  width: 32px;
  height: 32px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer; }
  .tile:active {
    background: #aaa; }
  .tile.flagged::before {
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
    content: "\F024";
    color: red; }
  .tile span {
    display: none; }
  .tile.open {
    background: #eee; }
    .tile.open span {
      display: inline; }
    .tile.open.bomb span {
      display: none; }
    .tile.open.bomb::before {
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
      content: "\F1E2";
      color: #333; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

